import React, { Component } from 'react'
import {Modal,Button,Menu,Form,Table,Icon} from 'semantic-ui-react'
import Culto from '../model/Culto'

class ModalLista extends Component {
    constructor(props) {
        super(props)
        this.state={open:false,value:'3',irmaos:[],irmas:[]}
    }

    componentDidMount = ()=>{
        
    }
    handleChange = (e, { value }) => this.setState({ value },()=>this.getCultos())

    getCultos = async()=>{
        let contexto = this
        const cultos = await Culto.fetchCultos()
        let culto = cultos.reduce((p,a)=>a.diasemana === this.state.value? a:p,{})
        let IrmaConfirmada = culto.capacidade.irma.filter(x =>x.status === true)
        let IrmaoConfirmado = culto.capacidade.irmao.filter(x =>x.status === true)
        console.log(IrmaConfirmada)
        this.setState({irmaos:IrmaoConfirmado,irmas:IrmaConfirmada})



    }

    handleOpen = ()=> this.setState({open:true},()=>this.getCultos())
    handleClose = ()=> this.setState({open:false})
    render () {
        return (
            <Modal
            centered={false}
            open={this.state.open}
            
            closeOnEscape ={false}
            closeOnDimmerClick = {false}
            trigger={<Menu.Item as='a' onClick={this.handleOpen}>Lista de Confirmação</Menu.Item>}
          >
            <Modal.Header>Lista de irmandade confirmada!</Modal.Header>
            <Modal.Content>
              <Modal.Description>
              <Form>
                          <Form.Group inline>
                          <label>Cultos</label>
                          <Form.Radio
                            label='Terça-Feira'
                            value='3'
                            name='radioGroup'
                            checked={this.state.value === '3'}
                            onChange={this.handleChange}
                          />
                          <Form.Radio
                            label='Quinta-Feira'
                            value='5'
                            name='radioGroup'
                            checked={this.state.value === '5'}
                            onChange={this.handleChange}
                          />
                        </Form.Group>
                    </Form>
                    <Table celled striped>
                    <Table.Header>
                    <Table.Row>
                        <Table.HeaderCell colSpan='3'>Confirmados</Table.HeaderCell>
                    </Table.Row>
                    </Table.Header>

                    <Table.Body>
                      {this.state.irmaos.map(item=>{return <Table.Row><Table.Cell>{item.visitante}</Table.Cell> </Table.Row>})}
                      {this.state.irmas.map(item=>{return <Table.Row><Table.Cell>{item.visitante}</Table.Cell> </Table.Row>})}

                        
                        
                       
                    </Table.Body>
                </Table>
              </Modal.Description>
            </Modal.Content>
            <Modal.Actions>
              <Button onClick={this.handleClose} >OK</Button>
            </Modal.Actions>
          </Modal>
        )
    }
}

export default ModalLista