import React, { Component } from 'react'
import {Grid,Segment,Image,Label,Form} from 'semantic-ui-react'
import Assento from './Assento'
import Lugar from '../assets/armchair.svg'
import Culto from '../model/Culto'


class RecantoMaravilha extends Component {

    constructor(props){
        super(props)
        this.state={layout:[],planejamento:[],value:'3',culto:{}}
    }
    //}

   confirmaPosicao = async(posicaoSelectd,visitante)=>{
        let cultoupdated = JSON.stringify(this.state.culto)
        cultoupdated =JSON.parse(cultoupdated)
        cultoupdated.capacidade[this.props.publico] = cultoupdated.capacidade[this.props.publico].map(item=>{
            if(item.id === posicaoSelectd){
                item.status = true
                item.visitante = visitante
            }
            return item
        })
        
        const checkCulto = await Culto.fetchById(cultoupdated)
        let aprovacao = false
        if(checkCulto.length > 0){
            aprovacao = checkCulto[0].capacidade[this.props.publico].reduce((p,a)=>a.id === posicaoSelectd && a.status === false ? true:p,false)
        }
        
       if(aprovacao){
        const result = await Culto.atualiza(cultoupdated)
        return true
       }else{
           return false
       }
      

    } 

    componentDidMount = ()=>{
        this.getCultos()
    }

    handleChange = (e, { value }) => this.setState({ value },()=>this.getCultos())

    getCultos = async()=>{
        let contexto = this
        const cultos = await Culto.fetchCultos()
        let culto = cultos.reduce((p,a)=>a.diasemana === this.state.value? a:p,{})
        this.setState({planejamento:culto.capacidade[this.props.publico],culto:culto},()=>{
            contexto.buildLayout()
        })
    }

    buildLayout = ()=>{
        let layout = this.state.planejamento.map((posicao,index)=>{
            return <Assento handleConfirma={this.confirmaPosicao} getCultos={this.getCultos}  posicao={posicao}/>

        })

        this.setState({layout:layout})
        
    }

    render () {
        return (
            <Grid>
                <Grid.Row>
                <Grid.Column width={16}>
                    <Form>
                          <Form.Group inline>
                          <label>Cultos</label>
                          <Form.Radio
                            label='Terça-Feira'
                            value='3'
                            name='radioGroup'
                            checked={this.state.value === '3'}
                            onChange={this.handleChange}
                          />
                          <Form.Radio
                            label='Quinta-Feira'
                            value='5'
                            name='radioGroup'
                            checked={this.state.value === '5'}
                            onChange={this.handleChange}
                          />
                        </Form.Group>
                    </Form>
                </Grid.Column>
                </Grid.Row>
                <Grid.Row>
                <Grid.Column width={16}>
                    <Label as='a' color='green' ><Image avatar  src={Lugar} /></Label> Ocupado
                    <Label as='a' color='' ><Image avatar  src={Lugar} /></Label> Livre
                </Grid.Column>
                </Grid.Row>
                <Grid.Row>
                <Label.Group size='huge'>
                {this.state.layout}
                </Label.Group>
                    
                </Grid.Row>
            </Grid>
        )
    }
}

export default RecantoMaravilha