import axios from 'axios';

var server = 'https://ccbconfirm.herokuapp.com'

class Culto {
    constructor(values){

    }
    static fetchCultos = async()=>{
        const cultos = await axios({method: 'GET',url:  `${server}/cultos/` })
        return cultos.data
    }

    static fetchById = async(culto)=>{
        const cultos = await axios({method: 'GET',url:  `${server}/cultos/ref/${culto._id}` })
        return cultos.data
    }

    static atualiza = async(culto)=>{
        const response = await axios({method: 'PUT',url:  `${server}/cultos/${culto._id}`,data:culto })
        console.log(response)
        return response.data
    }


}


export default Culto;