import React, { Component } from 'react'
import { Button, Header, Image, Modal,Label,Form,Popup,Message } from 'semantic-ui-react'
import Lugar from '../assets/armchair.svg'

const membros = [
    { key: '1', value: 'Silas Viana', text: 'Silas Viana' },
    { key: '2', value: 'Teneval Teodoro',  text: 'Teneval Teodoro' },
    { key: '3', value: 'Gildézio',  text: 'Gildézio' },
    { key: '4', value: 'Dalva',  text: 'Dalva' },
    { key: '4', value: 'Dalva',  text: 'Dalva' },
    { key: '4', value: 'Ivone',  text: 'Ivone' },
    { key: '4', value: 'Toninho',  text: 'Toninho' },
  ]

class Assento extends Component {
    constructor(props) {
        super(props)
        this.state={open:false,visitante:'',loading:false,erro:false}
    }

    handleOpen = ()=> this.setState({open:true})
    handleClose = ()=> this.setState({open:false})

    
    confirmaPosicao = async()=>{
      this.setState({loading:true})
        const result = await this.props.handleConfirma(this.props.posicao.id,this.state.visitante)
        console.log(result)
        if(result === true){
          console.log('boa')
          this.props.getCultos()
          this.handleClose()

        }else{
          console.log('ruim')
          this.setState({erro:true,loading:false})
          
        }
      
    }

    handleSelect = (e)=>{
      console.log(e.target.value)
        this.setState({visitante:e.target.value})
    }
    

    render () {
        return (
            <React.Fragment>
            {this.props.posicao.status
            ?  <Popup
            content={this.props.posicao.visitante}
            on='click'
            pinned
            trigger={<Label as='a' color={this.props.posicao.status ? 'green':''} onClick={this.handleOpen}><Image avatar  src={Lugar} /></Label>}
            
          />
          :
            <Modal
            open={this.state.open}
            trigger={<Label as='a' color={this.props.posicao.status ? 'green':''} onClick={this.handleOpen}><Image avatar  src={Lugar} /></Label>}
            closeOnEscape ={false}
            closeOnDimmerClick = {false}
          >
            <Modal.Header>Confirmação de Assento - POSICAO - {this.props.posicao.id}</Modal.Header>
            <Modal.Content image>
              <Image size='medium' src={Lugar} wrapped />
              <Modal.Description>
                <Header>Cadastre o visitante</Header>

              <Form>
                <Form.Field>
                  <label>Nome do visitante</label>
                  <input placeholder='nome completo' onChange = {this.handleSelect} value = {this.state.visitante} />
                </Form.Field>
              </Form>
              {this.state.erro ? <Message warning>Não foi possivel reservar , alguem acabou de reservar essa vaga recarregue a pagina para atualizar os lugares disponiveis</Message>: null}
              </Modal.Description>
            </Modal.Content>
            <Modal.Actions>
              <Button color='black' onClick={this.handleClose}>
                Cancelar
              </Button>
              <Button
                content="Confirmar presença"
                labelPosition='right'
                icon='checkmark'
                onClick = {this.confirmaPosicao}
                positive
                disabled = {this.state.visitante == ''}
                loading={this.state.loading}
              />
            </Modal.Actions>
          </Modal>}
          </React.Fragment>
        )
    }
}

export default Assento